<template>
	<div class="video">
		<div class="player">
			<video class="viewer " src="https://www.w3school.com.cn/i/movie.ogg"></video>
			<div class="player_controls">
				<div class="progress">
					<div class="progress_filled"></div>
				</div>	
				<button class="player_button"></button>
				<input type="range" name="volume" class="player__slider" min=0 max="1" step="0.05" value="1">
       			<button data-skip="-15" class="playerbutton">« 15s</button>
       			<button data-skip="15" class="playerbutton">15s »</button>
       			<button class="qp">全屏播放</button>
			</div>
		</div>
	</div>
</template>

<script>
	export default{
		mounted(){
			const player = document.querySelector('.player');
			const video = player.querySelector('.viewer');
			const progress = player.querySelector('.progress');
			const progressBar = player.querySelector('.progress_filled');
			const toggle = player.querySelector('.player_button');
			const ranges = player.querySelector('.player__slider');
			const skinbutton = player.querySelectorAll('.playerbutton');
			const qp = player.querySelector('.qp');

			function togglePlay() {
				video.paused ? video.play() : video.pause();
			}

			function updateButton() {
				toggle.style.background == 'green' ? toggle.style.background = 'white' 
				:toggle.style.background = 'green'
			}

			function skip(){
				video.currentTime += parseFloat(this.dataset.skip)
			}

			function handleRangeUpdate(){
				video.volume = this.value
			}

			function handleProgress() {
				const percent = (video.currentTime / video.duration) * 100;
				progressBar.style.width = `${percent}%`
			}

			function scrub(e) {
				const scrubTime = (e.offsetX / progress.offsetWidth) * video.duration;
				video.currentTime = scrubTime
			}

			function qpbf(){
				video.style.height =  window.innerHeight;
				alert('暂时不知道怎么弄横屏,')
			}

			video.addEventListener('timeupdate', handleProgress);
			toggle.addEventListener('click', togglePlay);
			toggle.addEventListener('click', updateButton);
			ranges.addEventListener('change', handleRangeUpdate);
			skinbutton.forEach(button => button.addEventListener('click', skip));
			qp.addEventListener('click', qpbf)
			progress.addEventListener('click', scrub);
		}
	}
</script>

<style lang="scss">
	.video{
		.player{
			position: relative;
			overflow: hidden;
		}
		.viewer{
			width: 100%;
			height: auto;
		}
		.player_controls{
			display: flex;
			position:absolute;
			bottom:.1rem;
			width: 100%;
			height: .5rem;
			transition: .3s;
			flex-wrap: wrap;
			.progress {
			  position: relative;
			  display:flex;
			  width: 100%;
			  height:.1rem;
			  transition:.3s;
			  cursor:ew-resize;
			  background:rgba(255,255,255,.4);
			.progress_filled {
				  background:white;
				  width: 0%;
				}
			}
			.player_button{
				margin:.1rem 0 0 .3rem;
				width: .3rem;
				height: .3rem;
				background:white;
			}
			.playerbutton{
				background:transparent;
				color:white;
				margin: 0 .2rem 0 .2rem;
				font-size: .28rem;
				height: .4rem;
				line-height: .4rem;
			}
			.qp{
				background:transparent;
				color:white;
				font-size: .26rem;
				height: .4rem;
				line-height: .4rem;
				margin-left: .4rem;
			}
			input[type=range] {
			  -webkit-appearance: none;
			  background:rgba(255,255,255,.4);
			  width: 30%;
			  height: .08rem;
			  margin: .2rem 0 0 .4rem;
			}
			input[type=range]::-webkit-slider-thumb {
			  width: .2rem;
			  height: .2rem;
			  border-radius: 50%;
			  background: white;
			  cursor: pointer;
			  -webkit-appearance: none;
			}
		}
	}
</style>